import React, { Component } from "react";
import {
  Container,
  Header,
  Image,
  Icon,
  Button,
  Grid,
  Divider
} from "semantic-ui-react";

class Section2 extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const { fullpageApi } = this.props;
    return (
      <Container>
        <Image src="..//static/images/ai-logo.png" />
        <Header as="h1" content="Artificial Intelligence" />
        <p>
          Artificial Intelligence enables machines to interact naturally with
          people, data and the environment. These systems create more intuitive
          interactions and extend the capabilities of what either human or
          machine can do on their own. From coding to training – AI enables
          intelligent systems that learn from a body of knowledge without
          analyzing and coding all business rules manually.
        </p>
        <Divider />
        <Grid>
          <Grid.Row columns={3}>
            <Grid.Column>
              <Header as="h3">
                <Icon name="settings" />
                <Header.Content>
                  What It Is
                  <Header.Subheader>
                    AI is a constellation of technologies—from machine learning
                    to natural language processing—that allows machines to
                    sense, comprehend, act and learn.
                  </Header.Subheader>
                </Header.Content>
              </Header>
            </Grid.Column>
            <Grid.Column>
              <Header as="h3">
                <Icon name="bullseye" />
                <Header.Content>
                  Why It Matters
                  <Header.Subheader>
                    Artificial intelligence will transform the relationship
                    between people and technology, charging our creativity and
                    skills.
                  </Header.Subheader>
                </Header.Content>
              </Header>
            </Grid.Column>
            <Grid.Column>
              <Header as="h3">
                <Icon name="angle double right" />
                <Header.Content>
                  Where It's Going
                  <Header.Subheader>
                    The future of AI promises a new era of disruption and
                    productivity, where human ingenuity is enhanced by speed and
                    precision.
                  </Header.Subheader>
                </Header.Content>
              </Header>
            </Grid.Column>
          </Grid.Row>
        </Grid>
        <div class="next-section arrow bounce">
          <Icon
            name="chevron down"
            onClick={() => fullpageApi.moveSectionDown()}
          />
        </div>
      </Container>
    );
  }
}

export default Section2;
